html {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  min-height: 100%;
  font-family: "Montserrat", "Tahoma", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
}

img {
  height: auto;
  max-width: 100%;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
.container {
  padding: 0 103px;
  margin: 0 auto;
  min-width: 1366px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
    min-width: 1023px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 calc(50% - 384px);
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
    min-width: 767px;
  }
}
@media (max-width: 320px) {
  .container {
    padding: 0 15px;
    min-width: 320px;
  }
}

.page-header {
  position: relative;
  padding: 0 103px;
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 0 45px;
    padding-top: 35px;
  }
}
@media (max-width: 767px) {
  .page-header {
    padding-right: 15px;
    padding-left: 15px;
  }
}

@media (max-width: 1023px) {
  .page-header__logo {
    width: 38px;
    height: 20px;
  }
}

.main-nav {
  position: relative;
  display: grid;
  grid-template-columns: 45px 1fr;
  align-items: center;
}

.main-nav__block {
  margin-left: auto;
}

.main-nav__toggle {
  position: absolute;
  top: -7px;
  right: 0px;
  width: 24px;
  height: 24px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:hover, .main-nav__toggle:focus {
  opacity: 0.7;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 6px;
    right: 1px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 8px 0 0 #f9fbfd, 0 -8px 0 0 #f9fbfd;
  }
}

@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__wrapper {
    position: relative;
    display: block;
    background-color: #f9fbfd;
    width: 320px;
    min-height: 568px;
    right: -21px;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__toggle {
    position: absolute;
    top: -20px;
    right: 0;
    width: 20px;
    height: 20px;
    background-color: transparent;
    cursor: pointer;
    border: none;
    z-index: 4;
  }
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 0;
  width: 18px;
  height: 2px;
  background-color: #011c40;
  z-index: 5;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav__logo {
  width: 70px;
  height: 38px;
  z-index: 2;
}
@media (max-width: 1023px) {
  .main-nav__logo {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .main-nav__logo {
    width: 42px;
    height: 22px;
  }
}

.main-nav__logo-popup {
  display: none;
}

.main-nav--opened .main-nav__logo {
  z-index: -2;
}

.main-nav--opened .main-nav__logo-popup {
  display: block;
  position: absolute;
  top: -20px;
  right: 180px;
  z-index: 5;
}

@media (max-width: 767px) {
  .main-nav__wrapper--show {
    position: absolute;
    top: -45px;
    right: -45px;
    margin-left: auto;
    padding-top: 75px;
    padding-right: 30px;
    padding-left: 25px;
    width: 285px;
    min-height: 568px;
    background-color: #f9fbfd;
    z-index: 3;
  }
}

.main-nav__wrapper ul {
  display: flex;
  flex-wrap: wrap;
  column-gap: 33px;
  margin: 0;
  list-style: none;
}
@media (max-width: 768px) {
  .main-nav__wrapper ul {
    column-gap: 23px;
  }
}
@media (max-width: 767px) {
  .main-nav__wrapper ul {
    flex-direction: column;
    justify-content: flex-start;
    padding: 0;
  }
}

@media (max-width: 767px) {
  .main-nav__wrapper li {
    margin-bottom: 18px;
  }
}

.main-nav__wrapper a {
  font-size: 16px;
  font-weight: 500;
  color: #f9fbfd;
}
.main-nav__wrapper a:hover, .main-nav__wrapper a:focus {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .main-nav__wrapper a {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .main-nav__wrapper a {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
  .main-nav__wrapper a:hover, .main-nav__wrapper a:focus {
    text-shadow: 0 0 3px #49b0d7;
    opacity: 1;
  }
}

